body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.timeline-container{
    background-color:#0f5677;
    width:100%;
    min-height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
}
.timeline{
    width:800px;
    background-color:#072736;
    color:#fff;
    padding:30px 20px;
    box-shadow:0px 0px 10px rgba(0,0,0,.5);
}
.timeline ul{
    list-style-type:none;
    border-left:2px solid #094a68;
    padding:10px 5px;
}
.timeline ul li{
    padding:20px 20px;
    position:relative;
    -webkit-transition:.5s;
    transition:.5s;
}
.timeline ul li span{
    display:inline-block;
    background-color:#1685b8;
    border-radius:25px;
    padding:2px 5px;
    font-size:15px;
    text-align:center;
}
.timeline ul li .content h3,h5{
    color:#34ace0;
    /* padding-top:5px; */
}
.timeline ul li .content p{
    padding:5px 0px 15px 0px;
    font-size:15px;
}
.timeline ul li:before{
    position:absolute;
    content:'';
    width:10px;
    height:10px;
    background-color:#34ace0;
    border-radius:50%;
    left:-11px;
    top:28px;
    -webkit-transition:.5s;
    transition:.5s;
}
.timeline ul li:hover{
    background-color:#071f2a;
}
.timeline ul li:hover:before{
    background-color:#0F0;
    box-shadow:0px 0px 10px 2px #0F0;
}
@media (max-width:300px){
    .timeline{
        width:100%;
        padding:30px 5px 30px 10px;
    }
    .timeline ul li .content h3,h5{
        color:#34ace0;
        font-size:15px;
    }

}

.skills-section{
padding-top: 20px;
}

.skills-header{
text-align: center;
color: #fff;
padding: 1rem;
position: relative;
}

.skills-header:after{
content: '';
position: absolute;
bottom: 0;
left: 50%;
-webkit-transform: translateX(-50%);
        transform: translateX(-50%);
width: 100px;
height: 4px;
background-color: #2ecc71;
border-radius: 2px;
}

.skills-container{
display: grid;
grid-tempLate-columns: repeat(5, 1fr);
justify-content:center;
align-items:center;
text-align: center;
grid-gap:1rem;
padding: 1rem 80px;
font-size: 1.2rem;
}
.skills-box{
padding: 1rem;
color: #000;
cursor:pointer;
}
.skills-box:hover > .skills-title:after,
.skills-box:hover > .skills-title:before{
width: 30px;
}
.skills-box:hover .skills-img{
-webkit-transform: translateX(-20px);
        transform: translateX(-20px);
}

.skills-title{
display:flex;
flex-direction: column;
align-items:center;
padding: 0.5rem;
margin-bottom: 0.5rem;
position: relative;
}



.skills-icons{
width:50px;
z-index:2;
}

.skills-img{
width:90px;
height:90px;
position: relative;
border-radius: 45px;
background-color: #fff;
display:flex;
justify-content:center;
align-items:center;
-webkit-transition: .5s;
transition: .5s;
}

.skills-img:after{
content: '';
position:absolute;
top:0;
left: 0;
width:50%;
height:90px;
background: rgba(100, 100, 100, 0.5);
border-radius: 45px 0 0 45px;
}
.skills-title h5{
color: #333;
margin-top: 0.5rem;
}
@media screen and (max-width:990px){
 .skills-container{
 grid-tempLate-columns: repeat(2, 1fr);
 padding: 2rem 50px;
 }
}

@media screen and (max-width:650px){
 .skills-container{
 grid-tempLate-columns:2fr;
 padding: 2rem 50px;
 }
}
.modal {
	position: relative;
	background-color: white;
	overflow-y: auto;
	max-width: 600px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 16px 32px;
	margin: 20vh auto;
}

.card-actions {
  display: flex;
  justify-content: space-around;
  margin-top: -20px;
}
.chip-container {
  display: flex;
  flex-flow: row wrap;
  height: 26px;
  /* pre-calculated */
  overflow: hidden;
}
.chip {
  margin: 2px 3px 0 1px;
  /* if top/bottom margins are changed, must calculate the chip-container's height again */
}
.description {
  height: 100px;
  overflow: hidden;
}
.project-image {
  height: 200px;
  width: 130px;
}
.MuiCardContent-root .MuiTypography-body2 {
  font-family: "Work Sans", sans-serif;
}
.MuiCardContent-root h2 {
  font-family: "Work Sans", sans-serif;
  font-weight: bold;
}
.MuiCardContent-root .MuiChip-colorPrimary {
  color: #fff;
  background-color: #3f51b5;
  border-radius: 4px;
}
.one-line {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

