.timeline-container{
    background-color:#0f5677;
    width:100%;
    min-height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
}
.timeline{
    width:800px;
    background-color:#072736;
    color:#fff;
    padding:30px 20px;
    box-shadow:0px 0px 10px rgba(0,0,0,.5);
}
.timeline ul{
    list-style-type:none;
    border-left:2px solid #094a68;
    padding:10px 5px;
}
.timeline ul li{
    padding:20px 20px;
    position:relative;
    transition:.5s;
}
.timeline ul li span{
    display:inline-block;
    background-color:#1685b8;
    border-radius:25px;
    padding:2px 5px;
    font-size:15px;
    text-align:center;
}
.timeline ul li .content h3,h5{
    color:#34ace0;
    /* padding-top:5px; */
}
.timeline ul li .content p{
    padding:5px 0px 15px 0px;
    font-size:15px;
}
.timeline ul li:before{
    position:absolute;
    content:'';
    width:10px;
    height:10px;
    background-color:#34ace0;
    border-radius:50%;
    left:-11px;
    top:28px;
    transition:.5s;
}
.timeline ul li:hover{
    background-color:#071f2a;
}
.timeline ul li:hover:before{
    background-color:#0F0;
    box-shadow:0px 0px 10px 2px #0F0;
}
@media (max-width:300px){
    .timeline{
        width:100%;
        padding:30px 5px 30px 10px;
    }
    .timeline ul li .content h3,h5{
        color:#34ace0;
        font-size:15px;
    }

}
